.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}

.time-count-down {
    width: 100%;
    height: 100%;
}

.close-btn {
    color: var(--main-text-color-2);
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    cursor: pointer;
}

.del-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--danger-color);
    font-weight: bold;
    cursor: pointer;
}

.ok-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--main-text-color-2);
    font-weight: bold;
    cursor: pointer;
}

.ok-btn.active {
    color: var(--success-color);
}

.div-mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: var(--mid-zindex) -1;
    background: var(--main-glass-color);
}

div {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.dc-biggerIn {
    animation: biggerIn 0.6s cubic-bezier(0.34, 1.56, 0.64, 1) both;
}

.dc-fadeIn {
    animation: fadeIn 0.5s ease-out;
}

@keyframes biggerIn {
    0% {
        opacity: 0;
        transform: scale(0.5);
    }
    50% {
        opacity: 1;
        transform: scale(1.05);
    }
    70% {
        transform: scale(0.95);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}
